html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:after,
*:before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Quicksand", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: #1d1d1d;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.contenedor {
  width: min(90%, 120rem);
  margin: 0 auto;
}

.bold {
  font-weight: bold;
}

.jump {
  display: none;
}
@media (min-width: 768px) {
  .jump {
    display: unset;
  }
}

.banner {
  pointer-events: none;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
header.header {
  width: min(95%, 120rem);
  margin: 0 auto;
  text-decoration: none;
}
header.header .header_nav {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
@media (min-width: 768px) {
  header.header .header_nav {
    flex-direction: row;
  }
  header.header .header_nav * {
    margin-bottom: 0;
  }
}
@media (min-width: 768px) {
  header.header .header_nav .mobile-menu {
    display: none;
  }
}
header.header .header_nav .mobile-menu img {
  width: 2.5rem;
}
header.header .header_nav .header_nav_logo {
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  header.header .header_nav .header_nav_logo {
    margin-bottom: unset;
  }
}
header.header .header_nav .header_nav_logo,
header.header .header_nav .header_nav_donar {
  background-color: transparent;
}
header.header .header_nav .header_nav_logo img,
header.header .header_nav .header_nav_donar img {
  max-height: 3rem;
  width: auto;
}
header.header .header_nav .header_nav_opciones {
  margin: 0;
  padding: 0;
  list-style: none;
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  header.header .header_nav .header_nav_opciones {
    visibility: visible;
    opacity: 1;
    height: auto;
  }
}
header.header .header_nav .header_nav_opciones li {
  position: relative;
  display: block;
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  header.header .header_nav .header_nav_opciones li {
    display: inline-block;
    margin-right: 1.5rem;
    margin-bottom: 0;
  }
  header.header .header_nav .header_nav_opciones li:not(:last-child)::after {
    content: "";
    position: absolute;
    top: 50%;
    right: -1rem;
    width: 2.5px; /* Grosor de la línea */
    height: 80%; /* Altura de la línea */
    background-color: #e30514;
    border-radius: 2px; /* Bordes curvos */
    transform: translateY(-50%); /* Centrar verticalmente */
  }
}
header.header .header_nav .header_nav_opciones li a {
  color: #1d1d1d;
  font-weight: bold;
  font-size: 1.4rem;
  transition: all 0.3s ease-in;
}
header.header .header_nav .header_nav_opciones li a:hover,
header.header .header_nav .header_nav_opciones li a .active {
  color: #bf1616;
}
header.header .header_nav .header_nav_opciones li:last-of-type {
  margin-right: 0;
}
header.header .header_nav .header_nav_opciones.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.webp .footer_donar {
  background-image: url(../img/banner_donativo.webp);
}

.no-webp .footer_donar {
  background-image: url(../img/banner_donativo.png);
}

.footer_donar {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.footer_donar .content {
  margin: 0 auto;
  width: min(95%, 90rem);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .footer_donar .content {
    flex-direction: row;
  }
}
.footer_donar .content h1 {
  color: #ffffff;
  font-weight: bold;
  text-align: center;
  font-size: 4rem;
}
@media (min-width: 768px) {
  .footer_donar .content h1 {
    font-size: 5.5rem;
  }
}
.footer_donar .content img {
  max-height: 10rem;
  width: auto;
}

footer {
  margin: 0 auto;
  background-color: #383434;
  padding: 1.5rem 0;
}
footer .footer_container {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  width: min(95%, 120rem);
  justify-content: space-evenly;
  align-items: stretch;
  gap: 1rem;
}
@media (min-width: 768px) {
  footer .footer_container {
    flex-direction: row;
    justify-content: space-between;
  }
}
footer .footer_container .footer_item {
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: center;
}
@media (min-width: 768px) {
  footer .footer_container .footer_item {
    text-align: unset;
  }
}
footer .footer_container .footer_item p {
  color: #ffffff;
  margin: 0;
  padding: 0;
  font-size: 1.6rem;
}
footer .footer_container .footer_item p img {
  display: inline-block;
  height: 1em;
  width: auto;
  vertical-align: middle;
}
footer .footer_container .footer_item .icons {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  gap: 1rem;
  justify-content: center;
}
@media (min-width: 768px) {
  footer .footer_container .footer_item .icons {
    justify-content: flex-start;
  }
}
footer .footer_container .footer_item .icons img {
  display: inline-block;
  height: 2.5rem;
  width: auto;
}

.que-hacemos {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  padding: 4rem 0;
}
.que-hacemos h2 {
  color: #bf1616;
  text-align: center;
}
.que-hacemos .acciones {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2.5rem;
}
@media (min-width: 480px) {
  .que-hacemos .acciones {
    gap: 3.5rem;
  }
}
@media (min-width: 768px) {
  .que-hacemos .acciones {
    gap: 4.5rem;
  }
}
.que-hacemos .acciones a {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  gap: 1.5rem;
  max-width: 15rem;
  width: 100%;
}
.que-hacemos .acciones a img {
  max-height: 5rem;
  width: auto;
}
.que-hacemos .acciones a h3 {
  color: #383434;
  text-align: center;
}

.promocional {
  background-color: #383434;
  margin: 0 auto;
  padding: 4rem 0;
}
.promocional .contenedor {
  display: flex;
  justify-content: center;
  align-items: center;
}
.promocional .contenedor h3,
.promocional .contenedor p {
  color: #ffffff;
  text-align: center;
  margin: 0;
}
.promocional .contenedor div {
  flex: 0 0 calc(85% - 1rem);
}
@media (min-width: 240px) {
  .promocional .contenedor div {
    flex: 0 0 calc(77.5% - 1rem);
  }
}
@media (min-width: 480px) {
  .promocional .contenedor div {
    flex: 0 0 calc(70% - 1rem);
  }
}
.promocional .contenedor div h3 {
  margin-bottom: 1.5rem;
}
.promocional .contenedor img {
  pointer-events: none;
  max-height: 1.5rem;
  width: auto;
  flex: 0 0 calc(7.5% - 1rem);
}
@media (min-width: 240px) {
  .promocional .contenedor img {
    flex: 0 0 calc(11.25% - 1rem);
    max-height: 3.25rem;
  }
}
@media (min-width: 480px) {
  .promocional .contenedor img {
    flex: 0 0 calc(15% - 1rem);
    max-height: 5rem;
  }
}

.como-ayudar {
  background-color: #f1f1f0;
  margin: 0 auto;
  padding: 4rem 0;
}
.como-ayudar h2 {
  color: #bf1616;
  text-align: center;
  margin-bottom: 4rem;
}
.como-ayudar .contenedor {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  flex-direction: row;
  row-gap: 3rem;
  column-gap: 5rem;
}
.como-ayudar .contenedor .como-ayudar_item {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  border-radius: 1rem;
  height: 22.5rem;
}
@media (min-width: 480px) {
  .como-ayudar .contenedor .como-ayudar_item {
    height: 30rem;
  }
}
.como-ayudar .contenedor .como-ayudar_item img {
  height: 15rem;
  width: auto;
  object-fit: cover;
  border-radius: 1rem;
}
@media (min-width: 480px) {
  .como-ayudar .contenedor .como-ayudar_item img {
    height: 20rem;
  }
}
.como-ayudar .contenedor .como-ayudar_item h3 {
  text-align: center;
  color: #bf1616;
  width: 100%;
  height: 100%;
  place-content: center;
}